import React from "react";
import {} from "prop-types";
import cx from "classnames";
import "./event-card.scss";
import ContentfulLink from "../ContentfulLink/ContentfulLink";
import { timeFormat } from "./timeFormat";
const propTypes = {};

// Saturday, May 28th 2022 3:00 PM

const EventCard = (props) => {
  const {
    type,
    theme,
    eventDate,
    eventImage,
    eventSubtitle,
    eventVenue,
    id,
    slug,
    tag,
    title,
    url,
    dateString,
    eventInformation,
    eventWebsite,
    eventAddress,
  } = props;

  const addressLink = eventAddress
    ? {
        children: eventAddress,
        title: eventAddress,
        to: `https://www.google.com/maps/place/${encodeURIComponent(
          eventAddress
        )}`,
        type: "external",
      }
    : false;

  const detialLink = url
    ? {
        children: (
          <span>
            {type == "upcoming" ? "Upcoming " : "Past "}
            Event Details
            {theme !== "dark" ? (
              <svg
                width="20"
                height="16"
                viewBox="0 0 20 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M19.7381 7.23741L14.138 0.437399C13.6746 -0.081229 12.9317 -0.130745 12.444 0.268531C11.9564 0.667807 11.876 1.47481 12.2754 1.9624L16.263 6.79989H1.20004C0.53728 6.79989 0 7.33717 0 7.99989C0 8.66264 0.53728 9.19988 1.20004 9.19988H16.263L12.2754 14.0374C11.876 14.525 11.9659 15.3206 12.444 15.7312C12.9371 16.1547 13.7387 16.0501 14.138 15.5624L19.7381 8.7624C20.1027 8.2498 20.0715 7.73309 19.7381 7.23741Z"
                  fill="currentColor"
                />
              </svg>
            ) : (
              ""
            )}
          </span>
        ),
        title: `${theme !== "dark" ? "Past " : ""}Event Details`,
        to: url,
        type: "internal",
      }
    : false;
  return (
    <article
      className={cx("event-card", { [`event-card--${theme}`]: theme })}
      aria-label={title}
    >
      <div className="event-card__content">
        {dateString && (
          <div className="event-card__venue">{`${timeFormat(dateString)}`}</div>
        )}
        {eventVenue && (
          <div className="event-card__venue">
            {" "}
            {eventVenue.venueName}
            {eventVenue.venueCity ? `, ${eventVenue.venueCity}` : ""}
            {eventVenue.venueState ? `, ${eventVenue.venueState}` : ""}
          </div>
        )}
        {title && <h3 className="event-card__title">{title}</h3>}
        {tag && <p className="event-card__tag">{tag.title}</p>}
        {eventSubtitle && (
          <p className="event-card__sub-title">{eventSubtitle}</p>
        )}
        <div className="event-card__info-box">
          {eventInformation && (
            <>
              <div className="event-card__info-label">Information</div>
              <div>
                <ContentfulLink {...eventInformation} />
              </div>
            </>
          )}
          {eventWebsite && (
            <>
              <div className="event-card__info-label">Website</div>
              <div>
                <ContentfulLink {...eventWebsite} />
              </div>
            </>
          )}
          {eventAddress && (
            <>
              <div className="event-card__info-label">Address</div>
              <div>
                <ContentfulLink {...addressLink} />
              </div>
            </>
          )}
        </div>
        {url && (
          <ContentfulLink
            className={cx({ "btn btn--big": theme === "dark" })}
            {...detialLink}
          />
        )}
      </div>
      {eventImage && (
        <div className="event-card__image-wrapper">
          {/* {dateString && <div className={cx("event-card__date", { "event-card__date--soon": !dateString?.includes("/") })}>{dateString}</div>} */}
          {eventImage && <div className="event-card__image">{eventImage}</div>}
        </div>
      )}
    </article>
  );
};

EventCard.propTypes = propTypes;
export default EventCard;
