import { format, compareAsc } from 'date-fns'

const formatDay = date => {
    switch(date.getDay()){
        case 0:{return "Sunday"}
        case 1:{return "Monday"}
        case 2:{return "Tueday"}
        case 3:{return "Wednesday"}
        case 4:{return "Thursday"}
        case 5:{return "Friday"}
        case 6:{return "Saturday"}
        default:{return ''}
    }
}

const formatMonth = date => {
    switch(date.getMonth()){
        case 0:{return "Jan"}
        case 1:{return "Feb"}
        case 2:{return "Mar"}
        case 3:{return "Apr"}
        case 4:{return "May"}
        case 5:{return "Jun"}
        case 6:{return "Jul"}
        case 7:{return "Aug"}
        case 8:{return "Sep"}
        case 9:{return "Oct"}
        case 10:{return "Nov"}
        case 11:{return "Dec"}
        default:{return ''}
    }
}

const formatDate = date => date.getDate()>9? date.getDate(): `0${date.getDate()}`;

const hourFormat = date =>{ 
    let hours = date.getHours();
    let minutes = date.getMinutes();
    let ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes < 10 ? '0'+minutes : minutes;
    let strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
};

export const timeFormat = dateString => {
        let date = new Date(dateString);
        let day = formatDay(date);
        let month = formatMonth(date);
    return `${day}, ${month} ${formatDate(date)} ${date.getFullYear()} ${hourFormat(date)}`
}