import * as R from "ramda";
import dayjs from "dayjs";
import calendar from "dayjs/plugin/calendar.js";
dayjs.extend(calendar);

const now = new Date();
const dayOfWeek = now.getUTCDay() // 0-6 sunday-saturday
const leftThisWeek = 6 - dayOfWeek;

export const isToday = (date) => {
    const d = new Date(date);
    return d.getDate() === now.getDate() &&
      d.getMonth() === now.getMonth() &&
      d.getFullYear() === now.getFullYear()
  }
  
  export const getDateString = (date) =>
    dayjs(date).calendar(null, {
      sameDay: '[Today]',
      nextDay: '[Tomorrow]',
      thisWeek: '[This Week]',
      nextWeek: '[Next Week]',
      lastDay: '[Yesterday]', 
      lastWeek: '[Last Week]', 
      sameElse: 'DD/MM/YYYY'
    });
  
  //date array functions
  export const getDate = (string) => new Date(string);
  export const getFutureEvents = () => (obj) => getDate(R.prop("eventDate")(obj)) >= now || isToday(R.prop("eventDate")(obj));
  export const getMonthsEvents = () => (obj) => getDate(R.prop("eventDate")(obj)) > now && getDate(R.prop("eventDate")(obj)).getUTCMonth() === now.getUTCMonth() || isToday(R.prop("eventDate")(obj));
  export const getYearsEvents = () => (obj) => getDate(R.prop("eventDate")(obj)) > now && getDate(R.prop("eventDate")(obj)).getUTCFullYear() === now.getUTCFullYear() || isToday(R.prop("eventDate")(obj));
  export const getPastEvents = () => (obj) => getDate(R.prop("eventDate")(obj)) < new Date() && !isToday(R.prop("eventDate")(obj));
  export const getUnixTime=()=>(obj)=> dayjs(obj).valueOf();