import { func, number } from "prop-types";
import React from "react";
import { getPagerPages } from "./pagerMath.js";
import * as R from "ramda";
import cx from "classnames";
import "./pagination.scss";

const propTypes = {
  totalPages: number,
  currentPage: number,
  onPageChange: func,
};

const defaultProps = {
  currentPage: 1,
  totalPages: 1,
};

const Pagination = props => {
  const { totalPages, currentPage, onPageChange } = props;

  function renderPage(label, pageNum) {
    const isDisabled =
      pageNum === currentPage || pageNum < 1 || pageNum > totalPages;

    const className = cx("pagination__button", "btn", "btn--big", {
      "pagination__button--current": pageNum === currentPage,
      "btn--disabled": isDisabled,
    });

    const key = label + pageNum;

    return isDisabled ? (
        <button key={key} className={className} aria-disabled="true">
          {label.length <= 1 ? `0${label}`: label}
        </button>
     ) : (
      <button
        className={className}
        key={key}
        onClick={() => onPageChange && onPageChange(pageNum)}
      >
        <span>
        {label.length <= 1 ? `0${label}`: label}
        </span>
      </button>
    );
  }

  const middlePages = getPagerPages({
    currentPage,
    totalPages,
    blockRange: 3,
    firstPage: 1,
  });

  // const shouldShowFirstDots = R.head(middlePages) >= 3; // 1 ... 3
  // const shouldShowLastDots = totalPages - R.last(middlePages) > 1;
  const shouldShowFirstPage = R.head(middlePages) !== 1;
  const shouldShowLastPage = R.last(middlePages) !== totalPages;

  return (
    <div className="pagination">
      <div className="pagination__pages">
        {renderPage(<span>Previous</span>, currentPage - 1)}
        {shouldShowFirstPage && renderPage("1", 1)}
        {/* {shouldShowFirstDots && <div className="pagination__dots">...</div>} */}
        {middlePages.map(page => renderPage(page.toString(), page))}
        {/* {shouldShowLastDots && <div className="pagination__dots">...</div>} */}
        {shouldShowLastPage && renderPage(totalPages.toString(), totalPages)}
        {renderPage(<span>Next</span>, currentPage + 1)}
      </div>
    </div>
  );
};

Pagination.defaultProps = defaultProps;
Pagination.propTypes = propTypes;
export default Pagination;
