import "./events-page.scss";
import * as R from "ramda";
import {
  getDateString,
  getFutureEvents,
  getMonthsEvents,
  getPastEvents,
  getUnixTime,
  getYearsEvents,
} from "../../common/dateHelpers.js";
import { graphql, useStaticQuery } from "gatsby";
import { useEffect, useState } from "react";
import BreadCrumb from "../BreadCrumb/BreadCrumb.jsx";
import Container100 from "../Container/Container100.jsx";
import ContainerBleed from "../../components/Container/ContainerBleed.jsx";
import EventCard from "./EventCard.jsx";
import Page from "../Page/Page.jsx";
import PageBanner from "../PageBanner/PageBanner.jsx";
import Pagination from "./Pagination.jsx";
import Select from "../Select/Select";
import SignUp from "../../components/SignUp/SignUp.jsx";
import TwoUpTextBanner from "../../components/TwoUpTextBanner/TwoUpTextBanner.jsx";
import extractImage from "../../common/extractImage";
import extractLink from "../../common/extractLink";
import extractRichText from "../../common/extractRichText.jsx";
import extractTwoUpTextBanner from "../../common/extractTwoUpTextBanner.js";
import useAOS from "../../common/useAOS.js";

const propTypes = {};

const pastEventsPerPage = 5;
const futureEventsPerPage = 5;

const EventsPage = (props) => {
  const [upcomingEventsPage, setUpcomingEventsPage] = useState(1);
  const [pastEventsPage, setPastEventsPage] = useState(1);
  const [activeFilter, setActiveFilter] = useState("all");
  const contentfulData = useStaticQuery(query);

  const allContentfulEvents = R.map(
    R.compose(
      R.applySpec({
        eventAddress: R.prop("eventAddress"),
        eventInformation: R.compose(extractLink, R.prop("eventInformation")),
        eventDate: R.prop("eventDate"),
        eventImage: R.compose(extractImage, R.prop("eventImage")),
        eventSubtitle: R.prop("eventSubtitle"),
        eventWebsite: R.compose(extractLink, R.prop("eventWebsite")),
        eventVenue: R.prop("eventVenue"),
        id: R.prop("id"),
        slug: R.prop("slug"),
        tag: R.prop("tag"),
        title: R.prop("title"),
        unixTime: (item) => getUnixTime(R.prop("eventDate")(item)),
        url: (item) => `/event/${R.prop("slug")(item)}`,
      }),

      R.prop("node"),
    ),
  )(contentfulData?.allContentfulEvent?.edges);

  const eventsPageContent = R.compose(
    R.evolve({
      upcomingEventsText: extractRichText,
      pastEventsText: extractRichText,
      eventListingPageTwoUpTextBanner: R.unless(
        R.isNil,
        extractTwoUpTextBanner,
      ),
    }),
    R.defaultTo({}),
  )(contentfulData?.contentfulEventListingPage);

  const {
    title,
    subtitle,
    upcomingEventsText,
    pastEventsText,
    id,
    eventListingPageTwoUpTextBanner,
  } = eventsPageContent;
  //get upcoming and past events
  const allUpcomingEvents = R.compose(
    R.sort(R.ascend(R.prop("unixTime"))),
    R.filter(getFutureEvents(R.values)),
  )(allContentfulEvents).sort((a,b) => {
    return new Date(a.eventDate).getTime() > new Date(b.eventDate).getTime()?1:-1
  });

  //years
  const yearsUpcomingEvents = R.compose(
    R.sort(R.ascend(R.prop("unixTime"))),
    R.filter(getYearsEvents(R.values)),
  )(allContentfulEvents);

  //months
  const monthsUpcomingEvents = R.compose(
    R.sort(R.ascend(R.prop("unixTime"))),
    R.filter(getMonthsEvents(R.values)),
  )(allContentfulEvents);

  //past
  const pastEvents = R.compose(
    R.sort(R.descend(R.prop("unixTime"))),
    R.filter(getPastEvents(R.values)),
  )(allContentfulEvents).sort((a,b) => {
    return new Date(a.eventDate).getTime() > new Date(b.eventDate).getTime()?-1:1
  });

  const setupEvents = (fn) => R.splitEvery(futureEventsPerPage, fn);

  //split into pages
  const [pastPages] = useState(R.splitEvery(pastEventsPerPage, pastEvents));
  const [upcomingPages, setUpcomingPages] = useState(
    setupEvents(allUpcomingEvents),
  );

  //set filter value on select change
  const handleChange = (change) => {
    if (R.includes("All")(change)) {
      setActiveFilter("all");
    } else if (R.includes("Month")(change)) {
      setActiveFilter("month");
    } else {
      setActiveFilter("year");
    }
  };

  const filterOpts = [
    ...(allUpcomingEvents.length > 0
      ? [`All Events (${allUpcomingEvents.length})`]
      : []),
    ...(monthsUpcomingEvents.length > 0
      ? [`This Month (${monthsUpcomingEvents.length})`]
      : []),
    ...(yearsUpcomingEvents.length > 0
      ? [`This Year (${yearsUpcomingEvents.length})`]
      : []),
  ];

  const selectProps = {
    items: filterOpts,
    itemToString: (d) => d,
    initialSelectedItem: filterOpts[0],
  };

  useEffect(() => {
    //set results items based on filter value
    if (activeFilter === "year") {
      setUpcomingPages(setupEvents(yearsUpcomingEvents.sort((a,b) => {
        return new Date(a.eventDate).getTime() > new Date(b.eventDate).getTime()?1:-1
      })));
    } else if (activeFilter === "month") {
      setUpcomingPages(setupEvents(monthsUpcomingEvents.sort((a,b) => {
        return new Date(a.eventDate).getTime() > new Date(b.eventDate).getTime()?1:-1
      })));
    } else {
      setUpcomingPages(setupEvents(allUpcomingEvents));
    }
  }, [activeFilter]);


  useAOS();
  return (
    <Page
      pageTitle={title}
      pageDescription={subtitle}
      className="listing-page events-listing-page"
    >
      <BreadCrumb pageId={id} />
      <PageBanner
        title={title}
        description={subtitle}
        accent={true}
        hasBackground={true}
      />
      <Container100>
        <div className="events-listing-page__filters">
          <div className="events-listing-page__content-section">
            {upcomingEventsText}
          </div>
          <Select
            selectProps={selectProps}
            onChange={(change) => handleChange(change)}
            label="Events By Date"
          />
        </div>
        {upcomingPages[upcomingEventsPage - 1]?.length && (
          <div
            className="upcoming-events-listing"
            role="region"
            aria-label="Upcoming Events"
          >
            <div className="upcoming-events-listing__items">
              {upcomingPages[upcomingEventsPage - 1].map((event, index) => {
                return <EventCard
                  {...event}
                  key={`upcoming_event_${index}`}
                  // theme="dark"
                  type="upcoming"
                  // dateString={getDateString(event.eventDate)}
                  dateString={event.eventDate}
                />
})}
            </div>
            <Pagination
              totalPages={upcomingPages.length}
              onPageChange={(current) => setUpcomingEventsPage(current)}
              currentPage={upcomingEventsPage}
            />
          </div>
        )}
        {pastPages[pastEventsPage - 1]?.length && (
          <div
            className="past-events-listing"
            role="region"
            aria-label="Past Events"
          >
            <div className="events-listing-page__content-section">
              {pastEventsText}
            </div>
            {pastPages[pastEventsPage - 1].map((event, index) => {
              return <EventCard
                {...event}
                key={`past_event_${index}`}
                dateString={event.eventDate}
                // dateString={getDateString(event.eventDate)}
                // theme="dark"
              />
            })}
            <Pagination
              totalPages={pastPages.length}
              onPageChange={(current) => setPastEventsPage(current)}
              currentPage={pastEventsPage}
            />
          </div>
        )}
      </Container100>
      {eventListingPageTwoUpTextBanner && (
        <ContainerBleed>
          <TwoUpTextBanner {...eventListingPageTwoUpTextBanner} />
        </ContainerBleed>
      )}

      <ContainerBleed>
        <SignUp
          title="Join Our Movement"
          subtitle="Sign up for our emails and learn how to get involved in Vision for Justice and help transform our criminal-legal system."
        />
      </ContainerBleed>
    </Page>
  );
};

EventsPage.propTypes = propTypes;
export default EventsPage;

const query = graphql`
  {
    contentfulEventListingPage(
      id: { eq: "0a03e606-f98d-5f8a-9641-fba2d635ea9d" }
    ) {
      id
      subtitle
      title
      pastEventsText {
        raw
      }
      upcomingEventsText {
        raw
      }
      eventListingPageTwoUpTextBanner {
        ...TwoUpTextBanner
      }
    }
    allContentfulEvent {
      edges {
        node {
          id
          title
          slug
          tag {
            title
          }
          eventDate
          eventSubtitle
          eventVenue {
            venueCity
            venueName
            venueState
          }
          eventImage {
            gatsbyImageData(
              jpegProgressive: true
              width: 495
              height: 335
              cropFocus: CENTER
              backgroundColor: "#000000"
              aspectRatio: 3.6666667
              quality: 80
            )
          }
          eventWebsite {
            ...Link
          }
        }
      }
    }
  }
`;
